.motivationalLink {
  text-decoration: none;
  color: black;
  display: none;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.motivationalLink.active {
  color: var(--black);
  display: block;
  padding: 6px;
}

.motivationalLink h1 {
  color: #5469e3;
  font-size: 50px;
}

.motivationalLink h1 span {
  color: #1b35cc;
  font-size: 50px;
}

.motivationalLink .quote-container {
  color: #1b35cc;
  font-size: 20px;
  padding: 0 200px;
}

@media (min-width: 768px) {
  .motivationalLink .quote-container {
    padding: 0 200px;
  }
}

body {
  background-color: #cbd2fd;
}

.motivationalDiv {
  cursor: pointer;
  padding: 10px;
  padding-right: 100px;
  padding-left: 100px;
  border: 1px solid #091b80;
  background-color: #1b35cc;

  background-image: -webkit-linear-gradient(
    left,
    #091b80,
    #1b35cc,
    #2142ff,
    #2131ff
  );

  background-image: linear-gradient(
    to right,
    #091b80,
    #1b35cc,
    #2142ff,
    #2131ff
  );
}

